import React from "react";
import { HashRouter, Routes, Route } from "react-router-dom";
import Home from "./pages/Home";
import { PlantProvider } from "./context/PlantContext";

function App() {
  return (
    <HashRouter>
      <PlantProvider>
        <Routes>
          <Route path="/" element={<Home />} />
        </Routes>
      </PlantProvider>
    </HashRouter>
  );
}

export default App;
